@import 'variables';

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

:root {
    font-size: 10px;
}

body {
    background-color: $bgColor;
}

.wrapper {
    background-color: $bgColor;
    background-image: url('./../img/wave_background.svg');
    background-blend-mode: soft-light;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    font-family: $supportFont;
    overflow: hidden;
}